'use client'

import { useEffect } from 'react'

/**
 * DeferredCSS component
 * Loads non-critical CSS asynchronously after initial render
 * This prevents render-blocking and improves critical path latency
 */
const DeferredCSS = () => {
  useEffect(() => {
    // Load CSS asynchronously after initial render
    // This allows the page to render with critical CSS first
    const loadCSS = () => {
      // Find all stylesheet links that are render-blocking
      const links = document.querySelectorAll('link[rel="stylesheet"]:not([data-deferred])')
      
      links.forEach((link) => {
        // Mark as deferred to avoid processing twice
        link.setAttribute('data-deferred', 'true')
        
        // Convert to async loading
        const href = link.getAttribute('href')
        if (href) {
          // Create new async link
          const asyncLink = document.createElement('link')
          asyncLink.rel = 'stylesheet'
          asyncLink.href = href
          asyncLink.media = 'print'
          asyncLink.onload = function() {
            this.media = 'all'
          }
          
          // Insert after the original link
          link.parentNode?.insertBefore(asyncLink, link.nextSibling)
          
          // Remove original blocking link after a delay
          setTimeout(() => {
            link.remove()
          }, 0)
        }
      })
    }

    // Use requestIdleCallback for better performance, fallback to setTimeout
    if ('requestIdleCallback' in window) {
      requestIdleCallback(loadCSS, { timeout: 2000 })
    } else {
      setTimeout(loadCSS, 100)
    }
  }, [])

  return null
}

export default DeferredCSS
